import { parseCSSAbsoluteLength } from '../cssUtils';

describe('Test parseCSSAbsoluteLength', () => {
  it('Test valid units', () => {
    expect(parseCSSAbsoluteLength('1px', 'px')).toBe('1px');
    expect(parseCSSAbsoluteLength('1pt', 'px')).toBe('1.3333333333333333px');
    expect(parseCSSAbsoluteLength('1pc', 'px')).toBe('16px');
    expect(parseCSSAbsoluteLength('1in', 'px')).toBe('96px');
    expect(parseCSSAbsoluteLength('1Q', 'px')).toBe('0.9448818897637795px');
    expect(parseCSSAbsoluteLength('1mm', 'px')).toBe('3.779527559055118px');
    expect(parseCSSAbsoluteLength('1cm', 'px')).toBe('37.79527559055118px');
  });

  it('Test incomplete units', () => {
    expect(parseCSSAbsoluteLength('1.', 'px')).toBe('1px');
    expect(parseCSSAbsoluteLength('.1', 'px')).toBe('0.1px');
    expect(parseCSSAbsoluteLength('.1pt', 'px')).toBe('0.13333333333333333px');
    expect(parseCSSAbsoluteLength('.1px', 'px')).toBe('0.1px');
    expect(parseCSSAbsoluteLength('.1pc', 'px')).toBe('1.6px');
    expect(parseCSSAbsoluteLength('1.pt', 'px')).toBe('1.3333333333333333px');
    expect(parseCSSAbsoluteLength('1.px', 'px')).toBe('1px');
    expect(parseCSSAbsoluteLength('1.pc', 'px')).toBe('16px');
  });

  it('Test invalid units', () => {
    expect(parseCSSAbsoluteLength('1..', 'px')).toBe('0px');
    expect(parseCSSAbsoluteLength('..1', 'px')).toBe('0px');
    expect(parseCSSAbsoluteLength('1..1', 'px')).toBe('0px');
    expect(parseCSSAbsoluteLength('xyz', 'px')).toBe('0px');
    expect(parseCSSAbsoluteLength('', 'px')).toBe('0px');
    expect(parseCSSAbsoluteLength('1em', 'px')).toBe('0px');
  });
});
